import React from 'react'
import Avatar from 'react-avatar';
import ScrollableAnchor from 'react-scrollable-anchor'
import Header from './header'
import Page from './page';
import Section from './section';

export default class Home extends React.Component {
	render() {
		return(
			<div>
				<Header />
				<Page>
					<ScrollableAnchor id={'top'}>
						<Section styles='hero'>
							<div className='background-image' style={{backgroundImage: `url(${process.env.PUBLIC_URL}/img/wallpaper-2819550.jpg)`}}></div>
							<div><Avatar src={`${process.env.PUBLIC_URL}/img/me1.jpg`} size={200} round={true} /></div>
							<h1>ShengTa Chung</h1>
						</Section>
					</ScrollableAnchor>	
					
					<ScrollableAnchor id={'projects'}>
            			<Section styles='our-work'>
							<h3 className='title'>Project</h3>
					    </Section>
          			</ScrollableAnchor>

					<ScrollableAnchor id={'background'}>
            			<Section styles='our-work'>
							<h3 className='title'>My Background</h3>
						</Section>
          			</ScrollableAnchor>
					
					<ScrollableAnchor id={'resume'}>
            			<Section styles='resume'>
              				<h3 className='title'>My Resume</h3>
						</Section>
          			</ScrollableAnchor>
					
				</Page>
			</div>
		)
	}
}



